@Component({
  selector: 'ngx-pages',
  template: `
    <ngx-sample-layout #sample>

      <nb-menu (itemSelected)="updateWaterUsage($event)" [items]="menu"></nb-menu>
      <router-outlet></router-outlet>
    </ngx-sample-layout>
  `,
})
export class PagesComponent {
  @ViewChild('sample') sample; 

  constructor(private service:PostsService){

  }
  updateWaterUsage(evt){
    this.service.filter(evt.title);    
  }

  menu = MENU_ITEMS;
}
